/* Bookcase - Clear Fix */
*,
*:after,
*:before {
  margin: 0;
  padding: 0;
/* Removes padding behaviour on widths */
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.clear:after {
  display: table;
  clear: both;
  content: "";
}
/* Bookcase - Grid */
.row:before,
.row:after,
.shelf:before,
.shelf:after {
  display: table;
  line-height: 0;
  content: "";
}
.row:after,
.shelf:after {
  clear: both;
}
.container:before,
.container:after,
.bookcase:before,
.bookcase:after {
  display: table;
  line-height: 0;
  content: "";
}
.container:after,
.bookcase:after {
  clear: both;
}
.container-wide:before,
.container-wide:after,
.bookcase-wide:before,
.bookcase-wide:after {
  display: table;
  line-height: 0;
  content: "";
}
.container-wide:after,
.bookcase-wide:after {
  clear: both;
}
.row,
.shelf {
  position: relative;
  margin-left: -10px;
  margin-left: -0.625rem;
  margin-right: -10px;
  margin-right: -0.625rem;
}
.container,
.bookcase {
  padding-left: 10px;
  padding-left: 0.625rem;
  padding-right: 10px;
  padding-right: 0.625rem;
  margin-left: auto;
  margin-right: auto;
}
.container-wide,
.bookcase-wide {
  width: 100%;
  padding-left: 0px;
  padding-left: 0rem;
  padding-right: 0px;
  padding-right: 0rem;
  margin-left: auto;
  margin-right: auto;
}
